},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-4 col-10 mb-lg-0 mb-4",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{staticClass:"img-fluid",attrs:{"src":require("@/assets/curso/temas/tema3/img4.png"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"zoom-in"}},[_c('p',[_vm._v("De acuerdo con lo anterior, en Colombia con la Ley 1996 de 2019 se da un marco normativo para que las personas con discapacidad puedan disfrutar plenamente de su capacidad, pudiendo elegir y tomar decisiones para todas y cada una de las actividades de la vida, entendiendo que el cambio de paradigma implica pasar de medidas sustitutivas —donde una tercera persona tomaba las decisiones de las personas con discapacidad (en mayor medida las personas con discapacidad intelectual se veían afectadas por este modelo de sustitución)— a un modelo donde se les guía y se les brindan los apoyos para que puedan tomar sus propias decisiones.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Para ello, se toma como referencia la concepción establecida en la Convención sobre los Derechos de las Personas con Discapacidad, que reconoce los siguientes aspectos:")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"col-lg-3 col-sm-6 col-8 mb-lg-0 mb-4",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img5.png"),"alt":"Texto que describa la imagen"}})])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La valoración de apoyos debe iniciar con una entrevista semiestructurada. Esto se debe a que la persona facilitadora del proceso realiza una etapa previa de preparación y aprestamiento, guiándose por el marco normativo y los lineamientos establecidos. En este sentido, las preguntas y el desarrollo de la entrevista no son improvisados, sino que responden a una planificación previa. No obstante, es fundamental que este espacio se configure como un escenario de conversación y diálogo, donde se garantice un ambiente seguro y de confianza que promueva la autonomía de la persona con discapacidad y contribuya a la eliminación de barreras actitudinales, físicas y comunicativas.")])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Por lo tanto, la entrevista no debe ser rígida ni limitarse a una lista de verificación. En cambio, se debe procurar un análisis integral del entorno familiar, de las condiciones físicas, sensoriales y psicoemocionales de la persona con discapacidad, así como de su interacción con el entorno cercano y con otras actividades de la vida cotidiana, como la escuela o el trabajo. Esta entrevista permite identificar elementos clave para el análisis de la autonomía, las barreras, las dificultades y las limitaciones que puedan existir en el entorno, según el nivel de funcionamiento de cada persona. Esto resulta esencial para determinar los ajustes razonables que deben implementarse, con el fin de que la persona con discapacidad pueda tomar decisiones y expresarlas libremente.")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"bloque-texto-c color-primario p-4"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-3 col-10 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img7.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-9"},[_c('p',{staticClass:"mb-0"},[_vm._v("Dado lo anterior, no existe un tiempo máximo establecido para la realización de la entrevista, ya que su duración dependerá de las circunstancias particulares de cada caso y de las herramientas de accesibilidad comunicativas que se requieran. La entrevista se lleva a cabo directamente con la persona con discapacidad y, si esta lo solicita y consiente, también puede incluir a su red de apoyo. Esta red suele coincidir con el núcleo familiar, y, en la medida de lo posible, se recomienda —previa coordinación con la familia y la Personería Municipal— realizar una visita domiciliaria. Esta visita permite conocer de manera más cercana las condiciones reales en las que vive la persona con discapacidad y facilita una comprensión más profunda de su entorno y de sus actividades cotidianas.")])])])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-9 order-lg-1 order-2",attrs:{"data-aos":"zoom-in"}},[_c('p',[_vm._v("Posteriormente, debe elaborarse un informe que recoja toda la información obtenida durante la entrevista. En dicho informe debe quedar claramente determinada la necesidad de apoyos que requiere la persona con discapacidad para el ejercicio pleno de su capacidad jurídica. A partir de este informe pueden derivarse dos posibles escenarios: por un lado, la no formalización de apoyos, dado que existen apoyos que no requieren ser formalizados —como aquellos que surgen de manera natural en el entorno familiar o comunitario—; por otro lado, se puede optar por la formalización de los apoyos, a través de mecanismos como el acuerdo de apoyo o la adjudicación judicial de apoyos.")])]),_c('div',{staticClass:"col-lg-3 col-10 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{staticClass:"img-fluid",attrs:{"src":require("@/assets/curso/temas/tema3/img8.png"),"alt":"Imagen decorativa"}})])])])
