},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-c color-secundario tarjeta mb-4"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-4 col-10 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img13.png"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"px-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La solicitud constituye el momento previo e inicial en el que una persona con discapacidad, o alguien perteneciente a su red de apoyo familiar o social, se acerca a entidades públicas o privadas que, de acuerdo con el artículo 11 de la Ley 1996 de 2019, están habilitadas para realizar la valoración de apoyos. Esto, siempre y cuando dichas entidades sigan los lineamientos y protocolos establecidos por el ente rector de la política pública de discapacidad —actualmente el Ministerio de Igualdad y Equidad—. Cabe aclarar que, como mínimo, las valoraciones de apoyo deben ser ofrecidas por la Defensoría del Pueblo, las Personerías y los entes territoriales a través de las gobernaciones y alcaldías.")])])])])])
